import React from 'react';
import {BottomNavigation ,BottomNavigationAction,makeStyles  } from '@material-ui/core';
import './footer.css'
import DonutLargeIcon from '@material-ui/icons/DonutLarge';
import HomeRoundedIcon from '@material-ui/icons/HomeRounded';
import TuneIcon from '@material-ui/icons/Tune';
import DescriptionRoundedIcon from '@material-ui/icons/DescriptionRounded';
import {withRouter}  from 'react-router-dom';
const useStyles = makeStyles({
    root: {
      width: '100%',
      // height:'70px',
      // position:'fixed',
      // bottom:0,
      height:'100%',
      display:'flex',
      justifyContent:'space-between',
      alignItems:"center",
      // borderTop:'1px solid rgb(117,117,117)',
      boxSizing:'border-box',
      background:"#fff",
       },
  });
  
  
 const Footer= (props:any)=>{
  const classes = useStyles();
  let val='recents'
  switch(props.location.pathname){
    case '/':
      val='recents';
      break;
      case '/water':
        val='favorites';
      break;
      case '/chart':
        val='nearby';
      break;
      case '/set':
        val='folder';
      break;
    
  }
  const [value, setValue] = React.useState(val);

  const handleChange = (event: React.ChangeEvent<{}>, newValue: string) => {
    setValue(newValue);
    var path:string=''
    switch(true){
        case newValue=='recents':
          path='/'
          break;
          case newValue=='favorites':
          path='/water'
          break;
          case newValue=='nearby':
          path='/chart'
          break;
          case newValue=='folder':
          path='/set';
          break;
        
    }
   props.history.push({pathname:path})
  };
console.log(props.location.pathname)
// React.useEffect(()=>{

// },[])

console.log(value)
  const tabBar=(path:string)=>{
    
  }
return <div className="footer">
  <BottomNavigation value={value} onChange={handleChange} className={classes.root} >
      <BottomNavigationAction label="首页" value="recents" style={{height:'50px'}}  icon={<HomeRoundedIcon style={{ fontSize: '30px'}} />} />
      <BottomNavigationAction label="流水" value="favorites"style={{height:'50px'}}  icon={<DescriptionRoundedIcon style={{ fontSize: '30px'}} />} />
      <BottomNavigationAction label="图表" value="nearby" style={{height:'50px'}}  icon={<DonutLargeIcon style={{ fontSize: '30px'}} />} />
      <BottomNavigationAction label="设置" value="folder" style={{height:'50px'}}  icon={<TuneIcon style={{ fontSize: '30px'}} />} />
    </BottomNavigation>
      </div>
};
export default withRouter(Footer);