import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';

export default function FormDialog(props:any) {
  const [open, setOpen] = React.useState(false);
  
  const inp:any=React.useRef(null)
  const handleClickOpen = () => {
    setOpen(true);
  };


  const [num,setNum]=React.useState('')
const getNum=(event:any)=>{
     setNum(event.target.value)
}

  const handleClose = () => {
    setOpen(false);
  };
  const handleAffirmClose=()=>{
          if(num!=''){
            if(props.budgetDate=='今年'){
              localStorage.setItem('yearBudGet',num)
            }else if(props.budgetDate=='本月'){
              localStorage.setItem('monthBudGet',num)
            }else if(props.budgetDate=='本周'){
              localStorage.setItem('weekBudGet',num)
            }else if(props.budgetDate=='今日'){
              localStorage.setItem('dayBudGet',num)
            }
          }
          props.resSumMoney(num)
          setOpen(false);
  }
  const {fref}=props
React.useImperativeHandle(fref,()=>({
    handleClickOpen
 
}))
  return (
    <div>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">添加预算</DialogTitle>
        <DialogContent>
          <TextField
            // autoFocus
            margin="dense"
            id="name"
            label="预算金额"
            type="number"
            fullWidth
            ref={inp}
            onChange={(event)=>getNum(event)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary" style={{ width: 30, height: 30, fontSize: 20 }}>
            取消
          </Button>
          <Button onClick={handleAffirmClose} color="primary" style={{ width: 30, height: 30, fontSize: 20 }}>
            确认
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
