const defaultStore = {
    list:[],
    text:''
}
const contentReducer= (state = defaultStore, action:any) => {
    const { type, payload } = action;
    switch(type){
        case 'FLAG_S':
            return{...state,text:payload.text};
        default: return state;
    }
    // console.log(type)
    // console.log(type=='FlAG_VALUE')
    // if(type=="FlAG_VALUE"){
    //     var newState=JSON.parse(JSON.stringify(state))
    //     newState.flag=payload.flag;
    //     return newState
    // }else
    // return state
}
export default contentReducer;