import React, { useState } from 'react';
import { DatePicker, Space ,ConfigProvider,TimePicker } from 'antd';
import 'antd/dist/antd.css';
import './data.css';
import zhCN from 'antd/es/locale/zh_CN';
import moment from 'moment';
import 'moment/locale/zh-cn';
moment.locale('zh-cn');
const weeks:string[]=['周日','周一','周二','周三','周四','周五','周六']
const data:any=new Date();
let year:number=data.getFullYear(),month:number | string =data.getMonth()+1,day:number| string=data.getDate(),week:number=data.getDay();
let hours:number| string=data.getHours(),minutes:number| string= data.getMinutes(),seconds:number| string= data.getSeconds();
if(month<10)month='0'+month;
if(day<10)day='0'+day;
if(hours<10)hours='0'+hours;
if(minutes<10)minutes='0'+minutes;
if(seconds<10)seconds='0'+seconds;
let selectData:string=year+'-'+month+'-'+day;
let selectTime:string=hours+':'+minutes+':'+seconds;
const  Data=(props:any)=> {
function DataonChange(date:any, dateString:string) {
  console.log(date, dateString);
  selectData=dateString;
  if(dateString!=""){
  week=new Date(dateString.replace('-',',')).getDay();
  }
  props.resData(selectData,selectTime,weeks[week]);
}

function TimeonChange(time:any, timeString:string) {
  console.log(time, timeString);
  selectTime=timeString;
  props.resData(selectData,selectTime,weeks[week]);
}
props.resData(selectData,selectTime,weeks[week]);
  return (
    <Space direction="vertical" style={{marginTop:55}} >
      <ConfigProvider locale={zhCN}>
    <DatePicker onChange={DataonChange} />
    <TimePicker
    onChange={TimeonChange}
    defaultOpenValue={moment('00:00:00', 'HH:mm:ss')}
  />
    </ConfigProvider>
  </Space>
  );
}

export default Data;
