import React, { useState, useRef, useEffect } from 'react';
import { withRouter } from 'react-router-dom'
import clsx from 'clsx';
import { createStyles, makeStyles, Theme, AppBar, Toolbar, Typography, Button, IconButton, SwipeableDrawer, ListItemText, ListItemIcon, ListItem, List, Divider, Icon, Slide, Dialog, TextField } from '@material-ui/core';
import { TransitionProps } from '@material-ui/core/transitions';
import CloseIcon from '@material-ui/icons/Close';
import SaveIcon from '@material-ui/icons/Save';
import MenuIcon from '@material-ui/icons/Menu';
import AccountBoxIcon from '@material-ui/icons/AccountBox';
import store from '../../store'
import { Select, Data, Label, Calculator } from './';
import './header.css'
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      flexGrow: 1,
      position: 'fixed',
      width: '100%',
      top: 0,
      zIndex: 1300
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      flexGrow: 1,
      fontSize: '20px',
    },
    appBar: {
      position: 'fixed',
      top: 0,
      height: 55,
      background: 'linear-gradient(45deg, #2196F3 30%, #21CBF3 90%)',
    },
    tool: {
      fontSize: '15px',
      background: 'linear-gradient(45deg, #2196F3 30%, #21CBF3 90%)',
    },
    icon: {
      fontSize: '25px'
    },
    list: {
      width: 250,
    },
    fullList: {
      width: 'auto',
    },
    button: {
      margin: theme.spacing(1),
      width: '100px',
      height: '30px',
      fontSize: '12px'
    },
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    textField: {
      marginLeft: theme.spacing(1),
      marginRight: theme.spacing(1),
      width: '100%',
      fontSize: 12,
      height: 30
    }
  }),
);
const Transition = React.forwardRef(function Transition(
  props: TransitionProps & { children?: React.ReactElement },
  ref: React.Ref<unknown>,
) {
  return <Slide direction="up" ref={ref} {...props} />;
});
type Anchor = "bottom" | "left" | "right" | "top"
const Header: React.FC<any> = (props: any) => {
  const classes = useStyles();
   let title='首页'
  switch(props.location.pathname){
    case '/':
      title='首页';
      break;
      case '/water':
        title='流水';
      break;
      case '/chart':
        title='图表';
      break;
      case '/set':
        title='设置';
      break;
    
  }
  const [open, setOpen] = React.useState(false);
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });
  let text: string = ''
  const handleClickOpen = () => {
    setOpen(true);

  };
  const [show, setShow] = useState('pays');
  const [money, setMoney] = React.useState('0')
  const [rrc, setRrc] = React.useState('')
  const getMoney = (money: string, rrc: string) => {
    if (show == 'pays') {
      setMoney('-' + money);
    } else if (show == 'incomes') {
      setMoney('+' + money)
    }
    setRrc(rrc)
  }
  let label: any;
  const getTag = (tag: any) => {
    label = tag
  }
  const handleClose = () => {
    setOpen(false);
  };

  const toggleDrawer = (anchor: Anchor, open: boolean) => (
    event: React.KeyboardEvent | React.MouseEvent,
  ) => {
    if (
      event &&
      event.type === 'keydown' &&
      ((event as React.KeyboardEvent).key === 'Tab' ||
        (event as React.KeyboardEvent).key === 'Shift')
    ) {
      return;
    }
    setState({ ...state, [anchor]: open });
  };
  const list = (anchor: Anchor) => (
    <div
      className={clsx(classes.list, {
      })}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        {['暂无用户'].map((text, index) => (
          <ListItem button key={text}>
            <ListItemIcon  >{<AccountBoxIcon />}</ListItemIcon>
            <ListItemText primary={text} style={{ fontSize: 12 }} />
          </ListItem>
        ))}
      </List>
      <Divider />
      <List>
      </List>
    </div>
  );
  let [payOrInCome, setPayOrInCome] = useState('')
  const getSelect = (str: string) => {

    setPayOrInCome(str)

  }
  interface DATE {
    date: string;
    time: string;
    week: string;
  }
  let dates: DATE = {
    date: '',
    time: '',
    week: ''
  }
  const getData = (date: string, time: string, week: string) => {
    dates.date = date;
    dates.time = time;
    dates.week = week;
  }

  useEffect(() => {
    if (payOrInCome == '支出') {
      setShow('pays')
    } else if (payOrInCome == '收入') {
      setShow('incomes')
    }
  }, [payOrInCome])
  const handleSave = () => {
    store.dispatch({
      type: 'FLAG_S',
      payload: {
        text: ''
      }
    })
    let pays: any = [];
    let incomes: any = [];
    console.log(localStorage.getItem(show) == null)
    if (localStorage.getItem(show) == undefined || localStorage.getItem(show) == null) {
      if (show == 'pays') {
        pays.push({ dates, label, rrc, money });
        localStorage.setItem(show, JSON.stringify(pays))
      } else if (show == 'incomes') {
        incomes.push({ dates, label, rrc, money })
        localStorage.setItem(show, JSON.stringify(incomes))
      }

    } else {
      if (show == 'pays') {
        pays = localStorage.getItem('pays');
        pays = JSON.parse(pays);
        pays.push({ dates, label, rrc, money });
        localStorage.setItem('pays', JSON.stringify(pays))
      } else if (show == 'incomes') {
        incomes = localStorage.getItem('incomes');
        incomes = JSON.parse(incomes);
        incomes.push({ dates, label, rrc, money });
        localStorage.setItem('incomes', JSON.stringify(incomes))
      }
    }
    setOpen(false);
    props.history.go()
  }
  store.subscribe(handleClickOpen);
  return (<div className={classes.root}>

    <AppBar position="static">
      <Toolbar className={classes.tool}>
        <IconButton edge="start" className={classes.menuButton} color="inherit" onClick={toggleDrawer('left', true)} aria-label="menu" title="1111">
          <MenuIcon className={classes.icon} />
        </IconButton>
        <Typography className={classes.title}>
          {title}
          </Typography>
        <Button variant="contained"
          color="primary"
          className={classes.button}
          onClick={handleClickOpen}
          endIcon={<Icon>send</Icon>}
        >记一笔</Button>
      </Toolbar>
    </AppBar>
    {(['left'] as Anchor[]).map((anchor) => (
      <React.Fragment key={anchor}>
        <SwipeableDrawer
          anchor={anchor}
          open={state[anchor]}
          onClose={toggleDrawer(anchor, false)}
          onOpen={toggleDrawer(anchor, true)}
        >
          {list(anchor)}
        </SwipeableDrawer>
      </React.Fragment>
    ))}
    <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
      <AppBar className={classes.appBar}>
        <Toolbar>
          <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
            <CloseIcon className={classes.icon} />
          </IconButton>
          <Typography variant="h6" className={classes.title}>
            <Select resSelect={getSelect} />
          </Typography>
          <Button
            variant="contained"
            color="primary"
            size="small"
            className={classes.button}
            onClick={handleSave}
            startIcon={<SaveIcon />}
          >
            保存
      </Button>
        </Toolbar>
      </AppBar>
      <List>
        <div><Data resData={getData} /></div>
        {payOrInCome != "" ? <div><Label payOrInCome={payOrInCome} resTag={getTag} /></div> : <div />}
        <div style={{ height: 420 }} ><Calculator resMoney={getMoney} /></div>
      </List>
    </Dialog>
  </div>)
};
export default withRouter(Header);