import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Statistics, Log } from '../layouts'
class Water extends Component<any, any>{
    constructor(props: any) {
        super(props);
        this.getDate = this.getDate.bind(this)
        let pays: any = localStorage.getItem('pays')!=undefined?localStorage.getItem('pays'):'[]';
        let incomes: any = localStorage.getItem('incomes')!=undefined?localStorage.getItem('incomes'):'[]';
        pays = JSON.parse(pays)
        incomes = JSON.parse(incomes)
        this.state = {
            year: new Date().getFullYear(),
            month: new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1,
            pays,
            incomes,
            monthSum: []
        }

        console.log(pays)
    }
    // readonly 
    flagState() {
        this.props.dispatch((dispatch: any) => {
            dispatch({
                type: 'FLAG_S',
                payload: {
                    text: '我是传过来的数据'
                }
            })
        })
    }
    componentWillMount() {

        let monthSum: string[] = []


        this.state.pays.forEach((v: any, i: number) => {
            console.log(v.dates.date.indexOf(this.state.year + '-' + this.state.month))
            if (v.dates.date.indexOf(this.state.year + '-' + this.state.month) != -1) {
                monthSum.push(v)
            }
        })
        this.state.incomes.forEach((v: any, i: number) => {
            if (v.dates.date.indexOf(this.state.year + '-' + this.state.month) != -1) {
                monthSum.push(v)
            }
        })
        this.setState({
            monthSum: [...monthSum]
        })
    }

    getDate(year: number, month: any) {
        this.setState({
            year,
            month
        },()=>{
            let monthSum: string[] = []
        this.state.pays.forEach((v: any, i: number) => {
            console.log(v.dates.date.indexOf(this.state.year + '-' + this.state.month))
            if (v.dates.date.indexOf(this.state.year + '-' + this.state.month) != -1) {
                monthSum.push(v)
            }
        })
        this.state.incomes.forEach((v: any, i: number) => {
            if (v.dates.date.indexOf(this.state.year + '-' + this.state.month) != -1) {
                monthSum.push(v)
            }
        })
        this.setState({
            monthSum: [...monthSum]
        })
        })
    }

    render() {
        let monthPay=0;
        let monthIncome=0;
        this.state.monthSum.forEach((v:any,i:number)=>{
                    console.log(v)
                    if(/^[-]/.test(v.money)){
                        monthPay+=parseFloat(v.money.replace('-',''))
                    }else if(/^[+]/.test(v.money)){
                        monthIncome+=parseFloat(v.money.replace('+',''))
                    }
        })
        return (
            <div>
                <div style={{ width: '100%', height:'5rem', background: 'url(./images/jingyu.jpg)', backgroundSize: '100% 100%' }}>
                    <Statistics resDate={this.getDate} flag={true}/>
        <div><h3 style={{fontSize:'15px',fontWeight:500,color:'rgb(250,254,255)',marginLeft:10,letterSpacing:' 0.02857em'}}>支出:   {monthPay}</h3></div>
        <div><h3 style={{fontSize:'15px',fontWeight:500,color:'rgb(250,254,255)',marginLeft:10,letterSpacing:' 0.02857em'}}>收入:  {monthIncome}</h3></div>
                </div>
                <Log monthSum={this.state.monthSum} />
            </div>
        )
    }


}
const mapStateToProps = (state: any, ownProps: any) => {
    return {
        list: state.content.list
    }
}
export default connect(mapStateToProps)(Water);