import React from 'react';
import { makeStyles, createStyles, Theme } from '@material-ui/core/styles';
import Popover from '@material-ui/core/Popover';
import Button from '@material-ui/core/Button';
import {Paper,ClickAwayListener,MenuList,MenuItem} from '@material-ui/core';
import DetailsRoundedIcon from '@material-ui/icons/DetailsRounded';
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    typography: {
      padding: theme.spacing(2),
    },
    button:{
        fontSize:14,
        textAlign:'right',
      },
  }),
);
const options = ['今年', '本月','本周', '今日',];
export default function SimplePopover(props:any) {
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);

  let index=0
      if(localStorage.getItem('budgetDate')==undefined){
    localStorage.setItem('budgetDate','今日')
}else{
    if(localStorage.getItem('budgetDate')=='今年'){
        index=0
    }else if(localStorage.getItem('budgetDate')=='本月'){
        index=1
    }else if(localStorage.getItem('budgetDate')=='本周'){
        index=2
    }else if(localStorage.getItem('budgetDate')=='今日'){
        index=3
    }
}
  const [selectedIndex, setSelectedIndex] = React.useState(index);
  const handleClick = (event: React.MouseEvent<HTMLButtonElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const handleMenuItemClick = (
    event: React.MouseEvent<HTMLLIElement, MouseEvent>,
    index: number,
  ) => {
    setSelectedIndex(index);
    setAnchorEl(null);
  };
  const open = Boolean(anchorEl);
  const id = open ? 'simple-popover' : undefined;
  React.useEffect(()=>{
      localStorage.setItem('budgetDate',options[selectedIndex])
    props.resDate(options[selectedIndex])
  },[options[selectedIndex]])
  console.log(options[selectedIndex])
  return (
    <div>
      <Button variant="text" className={classes.button} onClick={handleClick}>{options[selectedIndex]}预算<DetailsRoundedIcon style={{fontSize:17}}/></Button>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        transformOrigin={{
          vertical: 'top',
          horizontal: 'center',
        }}
      >
          <Paper>
                <ClickAwayListener onClickAway={handleClose}>
                  <MenuList id="split-button-menu">
                    {options.map((option, index) => (
                      <MenuItem
                        key={option}
                        disabled={index === selectedIndex}
                        selected={index === selectedIndex}
                        style={{fontSize:'15px'}}
                        onClick={(event) => handleMenuItemClick(event, index)}
                      >
                        {option}
                      </MenuItem>
                    ))}
                  </MenuList>
                </ClickAwayListener>
              </Paper>
      </Popover>
    </div>
  );
}
