import React from 'react';
import { createStyles, makeStyles, Theme } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import InputLabel from '@material-ui/core/InputLabel';
import Input from '@material-ui/core/Input';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    container: {
      display: 'flex',
      flexWrap: 'wrap',
    },
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
    },
  }),
);

export default function DialogSelect(props:any) {
  const classes = useStyles();
  let date=new Date();
  const [open, setOpen] = React.useState(false);
  const [ageOne, setAgeOne] = React.useState<number | string>(date.getFullYear());
  const [ageTwo, setAgeTwo] = React.useState<number | string |any>(date.getMonth()+1<10?'0'+(date.getMonth()+1):date.getMonth()+1);
  const handleChangeOne = (event: React.ChangeEvent<{ value: unknown }>) => {
    setAgeOne(Number(event.target.value) || '');
  };
  const handleChangeTwo = (event: React.ChangeEvent<{ value: unknown }>) => {
    setAgeTwo(event.target.value || '');
  };
//   console.log(ageOne,ageTwo)
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleAffirm= () => {
    setOpen(false);
    props.resDate(ageOne,ageTwo)
  };
  
  const handleClose = () => {
    setOpen(false);
  };
  let year:number[]=[]
  for(let i=1970;i<=2100;i++){
      year.push(i)
  }
  let month:number[]=[];
  for(let j=1;j<=12;j++){
      month.push(j)
  }
console.log(props)
  return (
    <div>
      <Button onClick={handleClickOpen} style={{color:props.flag?'#fff':'#000'}}>{ageOne}年 {ageTwo}月</Button>
      <Dialog disableBackdropClick disableEscapeKeyDown open={open} onClose={handleClose}>
        <DialogTitle>选择月份</DialogTitle>
        <DialogContent>
          <form className={classes.container}>
            <FormControl className={classes.formControl} style={{width:'100%'}}>
              <InputLabel htmlFor="demo-dialog-native">年</InputLabel>
              <Select
                value={ageOne}
                onChange={handleChangeOne}
                input={<Input id="demo-dialog-native" />}
                autoWidth={true}
              >
                {year.map((v,i)=>{
                    return <MenuItem key={i} value={v}>{v}</MenuItem>
                })}
              </Select>
            </FormControl>
            <FormControl className={classes.formControl} style={{width:'100%'}}>
              <InputLabel id="demo-dialog-select-label">月</InputLabel>
              <Select
                labelId="demo-dialog-select-label"
                id="demo-dialog-select"
                value={ageTwo}
                onChange={handleChangeTwo}
                input={<Input />}
              >
                {month.map((v,i)=>{
                    return <MenuItem key={i} value={v<10?'0'+v:v} >{v<10?'0'+v:v}</MenuItem>
                })}
              </Select>
            </FormControl>
          </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            取消
          </Button>
          <Button onClick={handleAffirm} color="primary">
            确认
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
