import React from 'react';
import { Progress } from 'antd';
import { Theme, makeStyles, createStyles, Paper, Divider } from '@material-ui/core'
import Button from '@material-ui/core/Button';
import store from '../../store';
import './set.css';
import { BudGet, BudGetSelect } from '../layouts'
const useStyles = makeStyles((theme: Theme) => {
  return createStyles({
    button: {
      fontSize: 14,
      textAlign: 'right',
    },
    root: {
      margin: theme.spacing(0.5),
    }
  })
})


export default function Set() {
  const classes = useStyles();
  const ref = React.useRef<any>()
  let sss:any='今日';
  if(localStorage.getItem('budgetDate')!==undefined && localStorage.getItem('budgetDate')!==null){
    sss=localStorage.getItem('budgetDate')
  }
  const [budgetDate, setBudDate] = React.useState(sss)
  const getDate = (str: string) => {
    setBudDate(str)
  }
  const date = new Date();
  const year = date.getFullYear();
  const month: any = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1;
  const week = date.getDay();
  const day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
  console.log(year, month, week, day)
  let all: any = '';
  let payAll: any[] = [];
  let paySum: number = 0
  let weeks: any[] = []
  for (let k = week - 1; k >= 0; k--) {
    var y: number | string = new Date(year, date.getDate() - k <= 0 ? parseInt(month) - 1 : parseInt(month), date.getDate() - k).getFullYear()
    var m: number | string = new Date(year, date.getDate() - k <= 0 ? parseInt(month) - 1 : parseInt(month), date.getDate() - k).getMonth()
    if (date.getDate() - k <= 0) {
      m += 1
    }
    if (m < 10) {
      m = '0' + m
    }
    var d: number | string = new Date(year, date.getDate() - k <= 0 ? parseInt(month) - 1 : parseInt(month), date.getDate() - k).getDate()
    if (d < 10) {
      d = '0' + d
    }
    weeks.push(y + '-' + m + '-' + d)
  }
  for (let l = 1; l <= 7 - week; l++) {

    console.log(new Date(year, parseInt(month), date.getDate() + l).getDate())
    var y: number | string = new Date(year, parseInt(month), date.getDate() + l).getFullYear()
    var m: number | string = new Date(year, parseInt(month), date.getDate() + l).getMonth()
    var d: number | string = new Date(year, parseInt(month), date.getDate() + l).getDate()
    if (m < 10) {
      m = '0' + m
    }
    if (d < 10) {
      d = '0' + d
    }
    weeks.push(y + '-' + m + '-' + d)
  }
  console.log(weeks)
  let str='dayBudGet'
  if (localStorage.getItem('pays') !== undefined && localStorage.getItem('pays')!==null) {
    all = localStorage.getItem('pays');
    all = JSON.parse(all);
    all.forEach((v: any, i: number) => {
     
      if (budgetDate == '今年') {
        str='yearBudGet'
        if (v.dates.date.indexOf('' + year) != -1) {
          payAll.push(v)
        }
      } else if (budgetDate == '本月') {
        str='monthBudGet'
        if (v.dates.date.indexOf(year + '-' + month) != -1) {
          payAll.push(v)
        }
      } else if (budgetDate == '本周') {
        str='weekBudGet'
        for (let i = 0; i < weeks.length; i++) {
          if (v.dates.date == weeks[i]) {
            payAll.push(v)
          }
        }
      }else if(budgetDate == '今日'){
        str='dayBudGet'
          if(v.dates.date==year+'-'+month+'-'+day){
            payAll.push(v)
          }
      }
    })
  }
  let allPayMoney:any=0;
  console.log(payAll)
  payAll.forEach((val:any,num:number)=>{
          allPayMoney+=parseFloat(val.money.replace('-',''))
  })
  console.log(allPayMoney)
 
let num:any=0

  if(localStorage.getItem(str)!=undefined){
  num=localStorage.getItem(str)
  num=Number(num)
}



  const [sumMoney, setSumMoney] = React.useState(num)
  React.useEffect(()=>{
    setSumMoney(num)
  },[num])
  console.log(str,num,sumMoney)
  const budget = () => {
    ref.current.handleClickOpen()
  }
  const getDateBudGet = () => {
    ref.current.handleClick()
  }
const getSumMoney=(num:any)=>{
  setSumMoney(Number(num));
}
  return (
    <div className="set">
      <Paper className={classes.root}>
        <div className="title"><div className="titleLeft"> <BudGetSelect resDate={getDate} /></div> <div className="titleRight"><Button variant="text" className={classes.button} onClick={budget}>编辑预算</Button></div></div>
        <div className="budget">
          <div className="left"><Progress type="circle" percent={Number(((1-allPayMoney/sumMoney)*100).toFixed(1))} format={percent => `余${percent+'%'}`} strokeWidth={10}
          /></div>
          <div className='right'>
            <div style={{ display: 'flex', justifyContent: 'space-between' }}><h4>剩余:</h4> <h4>{sumMoney-allPayMoney}</h4></div>
            <Divider />
  <div style={{ display: 'flex', justifyContent: 'space-between' }}><h5>预算:</h5> <h5>{sumMoney==0?'未设置'+budgetDate+'预算':sumMoney}</h5></div>
  <div style={{ display: 'flex', justifyContent: 'space-between' }}><h5>支出:</h5> <h5>{allPayMoney}</h5></div>
          </div>
        </div>
      </Paper>
      <BudGet fref={ref} budgetDate={budgetDate} resSumMoney={getSumMoney}/>

    </div>
  );
}