import React, { useImperativeHandle, useRef ,useEffect} from 'react';
import { createStyles, Theme, withStyles, WithStyles, makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import classes from '*.module.css';
import Avatar from '@material-ui/core/Avatar';
import Chip from '@material-ui/core/Chip';
import SettingsIcon from '@material-ui/icons/Settings';
import Paper from '@material-ui/core/Paper';
import { AddLabel } from '../layouts'
import './set_label.css'
const styles = (theme: Theme) =>
  createStyles({
    root: {
      margin: 0,
      padding: theme.spacing(2),
    },
    closeButton: {
      position: 'absolute',
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500],
    },
    title: {
      height: 50,
      fontSize: 12
    },
  });
const useStyles = makeStyles({
  span: {
    width: '30px',
    height: '30px'
  }
});
export interface DialogTitleProps extends WithStyles<typeof styles> {
  id: string;
  children: React.ReactNode;
  onClose: () => void;
}

const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose} style={{ width: '30px', height: '30px' }}>
          <CloseIcon style={{ width: '30px', height: '30px' }} />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme: Theme) => ({
  root: {
    margin: 0,
    padding: theme.spacing(1),
  },
}))(MuiDialogActions);
interface ChipData {
  key: number;
  label: string;
}
export default function CustomizedDialogs(props: any) {
  const classes = useStyles();
  let tags: any = props.tags
  // tags = JSON.parse(tags)
  const [chipData, setChipData] = React.useState<ChipData[]>(tags);
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    tags = localStorage.getItem(props.payOrInCome);
    setChipData(JSON.parse(tags));
    // props.updataChipData();
    setOpen(true);

  };
  let tagsTwo:any;
  const select=(two:any)=>{
      console.log(two)
      tagsTwo=two;
      
      setChipData([...tagsTwo])
     
  }
  const handleClose = () => {
    setOpen(false);
    tags = localStorage.getItem(props.payOrInCome);
    setChipData(JSON.parse(tags));
    props.updataChipData();

  };
  const handleConfirmCloce = () => {
    setOpen(false);
    if(tagsTwo==undefined){
    localStorage.setItem(props.payOrInCome, JSON.stringify(chipData))
    setChipData(chipData);
  }else{
    localStorage.setItem(props.payOrInCome, JSON.stringify(tagsTwo))
    setChipData(tagsTwo);
  }
    props.updataChipData();
    console.log(tagsTwo)
  }
  const { innerRef } = props;
  useImperativeHandle(innerRef, () => ({
    handleClickOpen
  }))
  const cref = useRef<any>();
  const trigger = () => {
    cref.current.handleClickOpen()
  }
  const handleDelete = (chipToDelete: ChipData) => () => {
    setChipData((chips) => chips.filter((chip) => chip.key !== chipToDelete.key));
    
    for(let i=chipToDelete.key+1;i<chipData.length;i++){
      
      // console.log(chipData)
      
      console.log( chipData[i].key-=1)
      console.log( chipData[i])
      
    }
   
  };
  return (
    <div>
      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
          管理标签
        </DialogTitle>
        <DialogContent dividers>
          <Typography gutterBottom>
            <Paper style={{ display: 'flex', justifyContent: 'space-around', flexWrap: 'wrap', alignItems: 'center', padding: '0 5px 0 5px' }}>
              {chipData.map((data, i) => {
                return (
                  <li key={data.key+data.label} style={{ float: 'left' }}>
                    <Chip
                      variant="outlined"
                      size="small"
                      avatar={<Avatar alt="Natacha" src="/static/images/avatar/1.jpg" />}
                      label={data.label}
                      color="primary"
                      onDelete={handleDelete(data)}
                      style={{ fontSize: 12 }}
                      disabled={data.key >=6 ? false : true}
                    />
                  </li>
                );
              })}
              <li>
                <Chip
                  variant="default"
                  size="small"
                  icon={<SettingsIcon color="primary" style={{ width: 15, height: 15 }}>M</SettingsIcon>}
                  label="新增标签"
                  color="primary"
                  clickable
                  onClick={trigger}
                  style={{ fontSize: 12 }}
                />
              </li>
            </Paper>
          </Typography>
        </DialogContent>
        <DialogActions><Button onClick={handleClose} color="primary" style={{ width: 30, height: 30, fontSize: 20 }}>
          取消
          </Button>
          <Button autoFocus onClick={handleConfirmCloce} color="primary" style={{ width: 30, height: 30, fontSize: 20 }}>
            确认
          </Button>
        </DialogActions>
        <AddLabel cref={cref} select={select} chipData={chipData} payOrInCome={props.payOrInCome}/>
      </Dialog>
    </div>
  );
}