import React, { Component, useRef, useEffect } from 'react';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import IconButton from '@material-ui/core/IconButton';
import BackspaceIcon from '@material-ui/icons/Backspace';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import Paper from '@material-ui/core/Paper';
import 'antd/dist/antd.css';
import TextField from '@material-ui/core/TextField';
const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        margin: {
            // margin: theme.spacing(1),
            width: '94%',
            height: '94%',
            // zIndex: 1000,
            background: '#fff',
            border: '1px solid rgb(63,81,181)',
            borderRadius: 5,
            boxSizing: 'border-box',
            fontWeight: 'bolder',
            fontSize: 24,
            outline: 'none'
        },
        confirm: {
            // margin: theme.spacing(1),
            fontSize: 24,
            width: '94%',
            height: '94%',
            background: 'rgb(63,81,181)',
            // zIndex:0,
            fontWeight: 'bolder',
            borderRadius: 5,
            color: '#fff',
            outline: 'none'

        },
        yes: {
            width: '94%',
            height: '200%',
            zIndex: 50,
            background: 'rgb(63,81,181)',
            border: '1px solid rgb(63,81,181)',
            borderRadius: 5,

        },
        extendedIcon: {
            marginRight: theme.spacing(1),
            width: '100%'
        },
        money: {
            background: '#f5f5f5',
            padding: '6px 12px',
            fontSize: '24px',
            lineHeight: '24px',
            fontFamily: 'Consolas,monospace',
            textAlign: 'right',
        },
    }),
);
const Calculator = (props:any) => {
    const classes = useStyles();
    let [num, setNum] = React.useState('0')
    let [newNum, setNewNum] = React.useState('')
    const [rrc,setRrc]=React.useState('')
const getVal=(e:any)=>{
    setRrc(e.target.value)
}
    const handleClick = (i: string) => {

        if (num != '0') {
            if (/[+ |-]/g.test(num) == false) {
                if (i == '+' || i == '-') setNum(num += i);
            }
            if (i == '+' || i == '-') {
                if (newNum.length >= 1) {
                    setNum(eval(num + newNum) + i);
                    setNewNum('');
                }
            }
            if (i == '=') {
                if (/([+ | -])$/.test(num + newNum) == true) {
                    var n = num.slice(0, num.length - 1);
                    if (n.length <= 0) n = '0';
                    setNum(n);
                    setNewNum('');

                } else {
                    setNum(eval(num + newNum) + '');
                    setNewNum('');
                }
            }
            if (i == 'X') {
                if (newNum.length > 0) {
                    var nn = newNum.slice(0, newNum.length - 1);
                    setNewNum(nn);
                } else {
                    var n = num.slice(0, num.length - 1);
                    if (n.length <= 0) n = '0';
                    setNum(n)

                }
            }

        };
        if (!/[+|-]/.test(num)) {

            if (!isNaN(Number(i))) {
                if (num.length < 8) {
                    if (Number(num) == 0) {
                        setNum(Number(num + i) + '')
                    } else {
                        setNum(num + i)
                    };
                }

            } else {
                if (i == '.') {
                    if (/[.]/g.test(num) == false) {
                        setNum(num += i)
                    }
                }

            }
        } else {
            if (!isNaN(Number(i))) {
                if (newNum.length < 8) {
                    if (newNum == '0') {
                        setNewNum(Number(newNum + i) + '')
                    } else {
                        setNewNum(newNum += i)
                    };
                }

            } else {
                if (i == '.') {
                    if (/[.]/g.test(newNum) == false) {
                        if (newNum == '') {
                            setNewNum(0 + i)
                        } else { setNewNum(newNum += i) }
                    }
                }
            }
        }
    }

    useEffect(() => {
        if(/([+ | -])$/.test(num + newNum) == true){
            var n = num.slice(0, num.length - 1);
            if (n.length <= 0) n = '0';
            props.resMoney(''+eval(n),rrc)
        }else {
            props.resMoney(''+eval(num + newNum),rrc)
        }
    })
 
    let btns: string[] = ['1', '2', '3', '+', '4', '5', '6', '-', '7', '8', '9', '=', '.', '0', 'X']

    return (
        <div style={{ width: '100%', position: 'fixed', bottom: 0, background: '#fff' }}>
            <Paper component="ul" style={{ width: '100%', height: '100%' }}>
                <TextField id="standard-basic" label="添加备注" fullWidth={true} size="small" onChange={(e)=>getVal(e)}/>
                <div className={classes.money} style={{ width: '100%', float: 'left' }}>{num}{newNum}</div>

                {btns.map((v, i) => {
                    return <li key={i} style={v != "=" ? { width: '25%', height: 80, float: 'left', display: 'flex', justifyContent: 'center', alignItems: 'center' } : { width: '25%', height: 160, float: 'right', display: 'flex', justifyContent: 'center', alignItems: 'center' }}>
                        <button
                            className={
                                classes.confirm
                            }
                            style={v == "=" ? { width: '94%', height: '96%', float: 'right' } : { height: '94%' }}
                            onClick={() => handleClick(v)}
                        >

                            {v == "X" ? <BackspaceIcon style={{ fontSize: 24 }}></BackspaceIcon> : v}
                        </button>
                    </li>
                })}
            </Paper>
        </div>
    )

}

export default Calculator;
