import React, { useImperativeHandle } from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Snackbar from '@material-ui/core/Snackbar';
import MuiAlert, { AlertProps } from '@material-ui/lab/Alert';
import { makeStyles, Theme } from '@material-ui/core/styles';
function Alert(props: AlertProps) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}

const useStyles = makeStyles((theme: Theme) => ({
  root: {
    width: '100%',
    '& > * + *': {
      marginTop: theme.spacing(2),
    },
  },
}));
export default function FormDialog(props: any) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [open2, setOpen2] = React.useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const handleClick = () => {
    setOpen2(true);
  };
  const handleClose2 = (event?: React.SyntheticEvent, reason?: string) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen2(false);
  };
  let val: string = ''
  const handleChange = (e: any) => {
    val = e.target.value.slice(0,8);
  }
  let tagsOne: any = localStorage.getItem(props.payOrInCome);
  let tagsTwo: any = props.chipData
  tagsOne = JSON.parse(tagsOne)
  const confirmAddLabel = () => {

    var arr1 = [];
    var arr2 = []
    for (var i = 0; i < tagsOne.length; i++) {
      arr1.push(tagsOne[i].label)
    }
    for (var j = 0; j < tagsTwo.length; j++) {
      arr2.push(tagsTwo[j].label)
    }
    if (val != undefined && val != '' && Number(val) != 0 && /^[ ]$/.test(val) != true) {
      if (arr1.indexOf(val) == -1) {
        tagsOne.push({
          key: tagsOne[tagsOne.length - 1].key + 1,
          label: val
        })
        tagsTwo.push({
          key: tagsTwo[tagsTwo.length - 1].key + 1,
          label: val
        })
        localStorage.setItem('tags', JSON.stringify(tagsOne))
        props.select(tagsTwo)
        setOpen(false);
      }else{
        handleClick();
        
      }

    }
     


  }
  const { cref } = props;
  useImperativeHandle(cref, () => ({
    handleClickOpen
  }))
  return (
    <div>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">新增标签</DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            id="name"
            label="标签名称"
            type="text"
            size="small"
            fullWidth
            style={{ fontSize: 15, fontWeight: 200 }}

            onChange={(e) => handleChange(e)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary" style={{ width: 30, height: 30, fontSize: 20 }}>
            取消
          </Button>
          <Button onClick={confirmAddLabel} color="primary" style={{ width: 30, height: 30, fontSize: 20 }}>
            确认
          </Button>
        </DialogActions>
        <Snackbar open={open2} autoHideDuration={3000} onClose={handleClose2}>
          <Alert onClose={handleClose2} severity="warning" style={{width:'100%',height:60,fontSize:20}}>
            你已经有这个标签了
        </Alert>
        </Snackbar>
      </Dialog>
    </div>
  );
}
