import React from 'react';
import { Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Divider from '@material-ui/core/Divider';
import {Paper} from '@material-ui/core'
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      fontWeight: theme.typography.fontWeightRegular,
    },
    listItemText: {
      fontSize: '18px',
    },
    text: {
      fontSize: 15
    },
    paper: {
      margin: theme.spacing(0.5),
    }
  }),
);

export default function SimpleAccordion(props: any) {
  const classes = useStyles();
  // console.log(props.monthSum)
  let arr: any = props.monthSum
  
  function unique(arr: any) {
    const res: any = new Map()
    return arr.filter((a: any) => !res.has(a) && res.set(a, 1))
  };
  function group(arr: any, k: any, b: any) {
    let allGroupName: any = arr.map((item: any) => {
      return item[k][b]
    })
    let typeList: any = unique(
      allGroupName
    );
    // console.log("类型", typeList)
    let list: any[] = []

    typeList.forEach((ele: any) => {
      let obj: any = {};
      obj.list = [];
      obj.list = arr.filter((sele: any) => ele == sele[k][b])
      obj.name = ele
      list.push(obj)
    })

    return list
  }
  let lists = group(arr, 'dates', 'date');
  lists.reverse()
  lists.forEach((v, i) => {
    return v.list.reverse()
  })
  console.log(props.monthSum,lists)
  return (
    <div className={classes.root}>
      <List>
        {lists.length > 0 ? lists.map((v: any, i: any) => {
          return <div key={v.list[0].dates.date+i}>
            <Paper className={classes.paper}><div style={{ width: '100%', height: 30, display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}><h5 style={{marginLeft:20}}>{v.name} {v.list[0].dates.week}</h5><h5 style={{marginRight:20}}>支出:{
              (function () {
                let payMoney = 0;
                v.list.map((val: any, num: any) => {
                  if (/^[-]/.test(val.money)) { payMoney += parseFloat(val.money.replace('-', '')) };
                })
                return <span>{payMoney}</span>
              }())
            }
               &nbsp;&nbsp;&nbsp;收入:{
              (function () {
                let payMoney = 0;
                v.list.map((val: any, num: any) => {
                  if (/^[+]/.test(val.money)) { payMoney += parseFloat(val.money.replace('-', '')) };
                })
                return <span>{payMoney}</span>
              }())
            }
              </h5></div></Paper>
            
            {v.list.map((item: any, index: any) => {
              return <div key={item.dates.time + index}>
                <ListItem button >
                  <ListItemAvatar>
                    <Avatar
                      alt="Natacha" src="/static/images/avatar/1.jpg"
                      style={{ width: 35, height: 35, fontSize: 35, background: 'rgb(48,63,159)' }}
                      variant="circle"
                    >
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText classes={{ primary: classes.listItemText, secondary: classes.listItemText }}
                    primary={
                      <div style={{ width: '100%', display: 'flex', justifyContent: 'space-between' }}>
                        <span>
                          {item.label.label}
                        </span>
                        <span>
                          {item.money}
                        </span>
                      </div>
                    }
                    secondary={
                      <div style={{ width: '100%', display: 'flex', justifyContent: 'space-between' }}>
                        <span >
                          {item.dates.time}
                        </span>
                        <span style={{ width: 100, overflow: 'hidden', whiteSpace: 'nowrap', textOverflow: 'ellipsis', textAlign: 'right' }}>
                          {item.rrc}
                        </span>
                      </div>
                    } style={{ fontSize: 12, color: '#000' }} />
                </ListItem>
                {v.list[index] != v.list[v.list.length - 1] ? <Divider variant="inset" component="li" /> : ''}
              </div>
            })}
          </div>
        }) : <div style={{ width: '100%', height: '100%', display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            <div style={{ textAlign: 'center' }}>
              <Avatar src="./icons/noData.svg" style={{ width: 100, height: 100, margin: '0 auto' }}></Avatar>
              <p>暂时还没有记录. 快去记一笔吧~</p>
            </div>

          </div>}
      </List>
    </div>
  );
}