import React from 'react'
import echarts from 'echarts'
import { Theme, createStyles, makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Divider from '@material-ui/core/Divider';
import { Statistics, Select } from '../layouts';
import { Paper } from '@material-ui/core'
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      width: '100%',
    },
    heading: {
      fontSize: theme.typography.pxToRem(15),
      fontWeight: theme.typography.fontWeightRegular,
    },
    listItemText: {
      fontSize: '18px',
    },
    text: {
      fontSize: 15
    },
    sel: {
      marginLeft: theme.spacing(3.2),
      zIndex: 1000,
      float: 'right',
      display: 'flex',
      justifyContent: 'center',
    },
    paper: {
      margin: theme.spacing(0.5),
      height: 30,
      display: 'flex',
      alignItems: 'center'
    }
  }),
);
const Chart = (props: any) => {
  const classes = useStyles();
  let pays: any = localStorage.getItem('pays') != undefined ? localStorage.getItem('pays') : '[]';
  let incomes: any = localStorage.getItem('incomes') != undefined ? localStorage.getItem('incomes') : '[]';
  pays = JSON.parse(pays)
  incomes = JSON.parse(incomes)
  const [year, setYear] = React.useState(new Date().getFullYear())
  const [month, setMonth] = React.useState(new Date().getMonth() + 1 < 10 ? '0' + (new Date().getMonth() + 1) : new Date().getMonth() + 1)
  const [pi, setPi] = React.useState(/^[-]/)
  let monthSum: string[] = []
  const getDate = (year: number, month: any) => {
    setYear(year)
    setMonth(month)
  }
  const [ppii, setPpii] = React.useState('消费')
  const getSelect = (str: string) => {
    if (str == '支出') {
      setPi(/^[-]/);
      setPpii('消费')
    } else {
      setPi(/^[+]/);
      setPpii('收益')
    }
  }
  console.log(pi)
  pays.forEach((v: any, i: number) => {
    console.log(v.dates.date.indexOf(year + '-' + month))
    if (v.dates.date.indexOf(year + '-' + month) != -1) {
      monthSum.push(v)
    }
  })
  incomes.forEach((v: any, i: number) => {
    if (v.dates.date.indexOf(year + '-' + month) != -1) {
      monthSum.push(v)
    }
  })

  console.log(monthSum)
  function unique(arr: any) {
    const res: any = new Map()
    return arr.filter((a: any) => !res.has(a) && res.set(a, 1))
  };
  function group(arr: any, k: any, b: any) {
    let allGroupName: any = arr.map((item: any) => {
      return item[k][b]
    })
    let typeList: any = unique(
      allGroupName
    );
    // console.log("类型", typeList)
    let list: any[] = []

    typeList.forEach((ele: any) => {
      let obj: any = {};
      let list2 = [];
      list2 = arr.filter((sele: any) => ele == sele[k][b])
      let num = 0
      list2.forEach((v: any) => {
        if (pi.test(v.money)) {
          num += parseFloat(v.money.replace('-', ''))
        }
      })
      obj.value = num
      obj.name = ele
      if (num != 0) {
        list.push(obj)
      }

    })

    return list
  }
  let lists: any = group(monthSum, 'label', 'label');
  lists = lists.sort(function (a: any, b: any) {
    return b.value - a.value
  })
  if (lists.length <= 0) {
    lists = [{ value: 1 }];
  }



  console.log(lists)

  const option = {
    tooltip: {
      trigger: 'item',
      formatter: function fn(params: any, ticket: any, callback: any) {
        let str: string = ''
        let val = ''
        if (ppii == '消费') {
          val = '-￥' + params.value
        } else {
          val = '+￥' + params.value
        }
        if (lists[0].name != undefined) {
          str = '<div id="toolTipBox" style="text-align:center;"><p style="font-size:20px;margin:0px;color:' + params.color + ';">' + params.name + '</p><p style="font-size:12px;margin:0px;color:' + params.color + ';">' + val + ' ' + params.percent + '%</p></div>'
        } else {
          str = '<div id="toolTipBox" style="text-align:center;"><p style="font-size:12px;margin:0px;color:' + params.color + ';">您本月还未记录' + ppii + '</p></div>'
        }
        return str
      },
      position: function (point: any, params: any, dom: any, rect: any, size: any) {
        var marginW = Math.ceil(size.contentSize[0] / 2);
        var marginH = Math.ceil(size.contentSize[1] / 2);
        dom.style.marginLeft = '-' + marginW + 'px'
        dom.style.marginTop = '-' + marginH + 'px'
        return ['50%', '50%']
      },
      backgroundColor: 'rgb(245,245,245)',
    },
    series: [
      {
        type: 'pie',
        radius: ['50%', '90%'],
        stillShowZeroSum: true,

        cursor: 'pointer',
        minAngle: 10,
        label: {
          position: 'inside',
          rotate: true,
          fontStyle: 'normal',
          backgroundColor: 'transparent',
        },
        data: lists
      }
    ]
  };

  const container: any = React.useRef(null)
  React.useEffect(() => {
    console.log(container)
    const myChart: any = echarts.init(container.current, 'light');
    myChart.setOption(option);
    let index = 0; // 高亮索引
    //   myChart.dispatchAction({
    //     type: "highlight",
    //     seriesIndex: index,
    //     dataIndex: index
    //   });
    //   myChart.on("mouseover", function(e:any) {
    //     if (e.dataIndex != index) {
    //       myChart.dispatchAction({
    //         type: "downplay",
    //         seriesIndex: 0,
    //         dataIndex: index
    //       });
    //     }
    //   });
    //   setTimeout(function(){
    //     myChart.dispatchAction({
    //         type: 'showTip',
    //         seriesIndex:0,  // 显示第几个series
    //         dataIndex: 0 // 显示第几个数据
    //     });
    // })
  })

  lists.sort((a: any, b: any) => {
    return b.value - a.value
  })
  let sum = 0;
  for (let i = 0; i < lists.length; i++) {
    sum += lists[i].value
  }
  console.log(monthSum)
  return (
    <div>
      <div style={{ display: 'flex' }}><div><Statistics resDate={getDate} flag={false} /></div> <div className={classes.sel}><Select resSelect={getSelect} /></div></div>
      <div style={{ width: '100%', height: 400, zIndex: 100 }} ref={container}></div>
      <List>
        <Paper
          className={classes.paper}
        >
          {/* <div> */}
            <h5
              style={{ marginLeft: 20 }}
            >
              {lists[0].name == undefined ? '您本月还未记录' + ppii : year + '年 ' + month + '月' + ppii + '排行榜'}
            </h5>
          {/* </div> */}
        </Paper>
        {lists[0].name != undefined ? lists.map((item: any, i: number) => {
          return <div key={item + i}>
            <ListItem  >
              <ListItemAvatar>
                <Avatar
                  alt="Natacha" src="/static/images/avatar/1.jpg"
                  style={{ width: 35, height: 35, fontSize: 35, background: 'rgb(48,63,159)' }}
                  variant="circle"
                >
                </Avatar>
              </ListItemAvatar>
              <ListItemText classes={{ primary: classes.listItemText, secondary: classes.listItemText }}
                primary={
                  <div style={{ width: '100%', display: 'flex', justifyContent: 'space-between' }}>
                    <span>
                      {item.name + ' ' + (item.value / sum * 100).toFixed(2) + '%'}
                    </span>
                    <span>
                      {ppii == '消费' ? '-' + item.value : '+' + item.value}
                    </span>
                  </div>
                }
                secondary={<div style={{ width: '100%', height: 15, }}><div style={{ width: (item.value / sum * 100).toFixed(2) + '%', height: '100%', borderRadius: '7.5px', background: 'rgb(48,63,159)' }}></div></div>} style={{ fontSize: 12, color: '#000' }} />

            </ListItem>
            <Divider variant="fullWidth" component="li" />
          </div>
        }) : <div />}
      </List>
    </div>

  )
}

export default Chart
