import React, { useRef, useEffect,useImperativeHandle } from 'react';
import { createStyles, Theme, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Avatar from '@material-ui/core/Avatar';
import Chip from '@material-ui/core/Chip';
import SettingsIcon from '@material-ui/icons/Settings';
import { SetLabel } from '../layouts'
interface ChipData {
  key: number;
  label: string;
}
const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      justifyContent: 'center',
      flexWrap: 'wrap',
      '& > *': {
        margin: theme.spacing(0.5),
      },

    },
    chip: {
      margin: theme.spacing(0.5),
      fontSize: '12px'
    },
  }),
);

export default function SmallOutlinedChips(props: any) {
  const classes = useStyles();
  let paytags: any = [
    { key: 0, label: '饮食' },
    { key: 1, label: '交通' },
    { key: 2, label: '居住' },
    { key: 3, label: '娱乐' },
    { key: 4, label: '购物' },
    { key: 5, label: '学习' },
    { key: 6, label: '运动' },
    { key: 7, label: '通讯' },
    { key: 8, label: '酒水' },
    { key: 9, label: '医疗' },
    { key: 10, label: '还款' },
    { key: 11, label: '旅游' },
  ]
  let incometags = [
    { key: 0, label: '工资' },
    { key: 1, label: '兼职' },
    { key: 2, label: '礼金' },
    { key: 3, label: '理财' },
    { key: 4, label: '报销' },
    { key: 5, label: '借款' },
    { key: 6, label: '其他' },
  ]
  let tags:any;
  const [payOrInCome,setPayOrInCome]=React.useState(
 'paytags'
)
  useEffect(()=>{
    if(props.payOrInCome=='支出'){
    setPayOrInCome(
      'paytags'
   )
   
  }else if(props.payOrInCome=='收入'){
    
    setPayOrInCome(
       'incometags'
       )
       
    }
  },[props.payOrInCome])
    if (localStorage.getItem(payOrInCome) == undefined || localStorage.getItem(payOrInCome) == null || localStorage.getItem(payOrInCome)=='{}') {
      if(payOrInCome=='paytags'){
        localStorage.setItem(payOrInCome, JSON.stringify(paytags))
      }else if(payOrInCome=='incometags'){
        localStorage.setItem(payOrInCome, JSON.stringify(incometags))
      }
       tags = localStorage.getItem(payOrInCome)
    } else {
      tags = localStorage.getItem(payOrInCome)
    }
  tags = JSON.parse(tags) 
  const [chipData, setChipData] = React.useState<ChipData[]>(tags);
  // const handleDelete = (chipToDelete: ChipData) => () => {
  //   setChipData((chips) => chips.filter((chip) => chip.key !== chipToDelete.key));
  // };
 
   const updataChipData = () => {
    tags = localStorage.getItem(payOrInCome)
    setChipData(JSON.parse(tags))
  }
  let [index, setIndex] = React.useState(0);
  const handleClick = (i: number, event: any) => {
    event.stopPropagation()
    event.nativeEvent.stopImmediatePropagation();
    setIndex(i)
    return undefined;
  };
  const ref = useRef<any>();
  const trigger = () => {
    ref.current.handleClickOpen()
  }
 
props.resTag(tags[index])
// useImperativeHandle(getRef, () => ({
//   updataChipData
// }))
  return (
    <div className={classes.root}>
      <Paper component="ul" className={classes.root}>
        {tags.map((data:any, i:any) => {
          return (
            <li key={data.key} >
              <Chip
                variant="outlined"
                size="small"
                avatar={<Avatar alt="Natacha" src="/static/images/avatar/1.jpg" />}
                label={data.label}
                onClick={(event) => handleClick(i, event)}
                className={classes.chip}
                clickable={false}
                color={index == data.key ? 'primary' : 'default'}
              />
            </li>
          );
        })}
        <li>
          <Chip
            variant="default"
            size="small"
            icon={<SettingsIcon color="primary" style={{ width: 15, height: 15 }}>M</SettingsIcon>}
            label="管理标签"
            color="primary"
            className={classes.chip}
            onClick={trigger}
            clickable
          />
        </li>
       
      </Paper>
      <SetLabel innerRef={ref} updataChipData={updataChipData} tags={tags}  payOrInCome={payOrInCome}/>
    </div>
  );
}
